var _self;
var timer;

export default{
	
	data() {
		return {
			phone1:'',
			phone2:'',
			showNext:false,
			canGet:true,
			canGet2:true,
			time1:60,
			loginInfo:{},
			code1:'',
			code2:'',
			timeBack:3,
			okTip:false
		}
	},
	onLoad(opt) {
		_self = this;
		if(opt.phone != undefined){
			_self.phone1 = opt.phone;
		}
		
		var loginRes = this.checkLogin('../index/personal', '2');
		if(!loginRes){return false;}
		this.loginInfo = loginRes;
		
	},
	components:{
	},
	methods:{
		//获取手机验证码
		getCodeOne(flag){
			//flag = 1原手机 ，其他值新手机
			var phoneNum = "";
			if(flag == 1){
				phoneNum = _self.phone1;
			}else{
				phoneNum = _self.phone2;
				
				if(phoneNum == _self.phone1){
					_self.tipMsg("请绑定新的手机号");
					return;
				}
				
				if(!_self.checkPhone(phoneNum)){
					return;
				}
				
			}
			
			uni.request({
			    url: _self.$httpUrl+'api/Usersup/sendCode', 
				method:"POST",
				data:{
					mobile:phoneNum
				},
			    success: (res) => {
			     
				  uni.showToast({
				  	title:res.data.msg,
				  	icon:'none'
				  })
				  
				  if(res.data.status == 1){
					  _self.canGet = false;
					  _self.canGet2 = false;
					  _self.timeDown();
				  }
				  
			    },fail: (e) => {
					
			    },complete: (e) => {
					
			    }
			});
			
		},
		//修改手机号
		updatePhone(flag){
			
			var pcode = '';
			var phoneNum = "";
			if(flag == 1){
				pcode = _self.code1;
				phoneNum = _self.phone1;
				
			}else{
				pcode = _self.code2;
				phoneNum = _self.phone2;
				
				if(phoneNum == _self.phone1){
					_self.tipMsg("请绑定新的手机号");
					return;
				}
				
				if(!_self.checkPhone(phoneNum)){
					return;
				}
			}
			
			if(pcode.trim() == ""){
				_self.tipMsg("请输入验证码");
				return;
			}
			
			uni.request({
			    url: _self.$httpUrl+'api/Usersup/modifyPhone', 
				method:"POST",
				data:{
					code:pcode,
					user_id:_self.loginInfo.uid,
					username:phoneNum
				},
			    success: (res) => {
			     
			      console.log(res);
				  
				  if(res.data.status == 1){
					if(flag == 1){
						_self.showNext=true;
						clearInterval(timer);
						_self.time1 = 60;
						_self.canGet2 = true;
					}else{
						_self.okTip = true;
						var backT = setInterval(function(){
							_self.timeBack = _self.timeBack - 1;
							if(_self.timeBack < 1){
								clearInterval(backT);
								uni.navigateBack({
									delta:1
								})
							}
						},1000)
						
					}
				  }else{
					  uni.showToast({
						title:res.data.msg,
						icon:'none'
					  })
				  }
				  
			    },fail: (e) => {
					
			    },complete: (e) => {
					
			    }
			});
			
		},
		//倒计时
		timeDown(){
			clearInterval(timer);
			_self.time1 = 60;
			timer = setInterval(function(){
				_self.time1 = _self.time1 - 1;
				if(_self.time1 < 1){
					clearInterval(timer);
					_self.canGet = true;
					_self.canGet2 = true;
					_self.time1 = 60;
				}
			},1000);
		}
		
		
	}
	
	
	
}


